.works-titles .underline {
  position: relative;
  z-index: 1;
}
.works-titles .underline::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 108px;
  height: 6px;
  background-image: url("../../images/decos/underline.png");
  background-size: cover;
  z-index: -1;
}
.works-titles__sentence {
  position: relative;
}
