  content: "";
  position: absolute;
  width: 64px;
  height: 64px;
}
@media (min-width: 1400px) {
  .works-titles__sentence::before {
    width: 72px;
    height: 72px;
  }
}
.works-titles__sentence:first-child::before {
  top: -60px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../../images/icon/quote-left.png");
  background-size: cover;
}
@media (min-width: 1400px) {
  .works-titles__sentence:first-child::before {
    left: 34%;
